import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { environment } from 'src/environments/environment.prod';

@Injectable({
  providedIn: 'root'
})
export class PaymentIntegrationService {
  apiRazor = environment.razor_apiUrl;
  api = environment.apiMokshUrl;

  constructor(private http: HttpClient) { }

  createRazorTestOrder(body: any) {
    //const url = this.api + 'WSGeneral.asmx/GenerateRazorOrderID';
    const url = this.api + 'WSGeneral.asmx/GenerateRazorTestOrderID';
    const headers = new HttpHeaders({ 'Content-Type': 'application/json' });
    return this.http.post<any>(url, body.toString(), { headers }).toPromise();
  }

  createRazorOrder(body: any) {
    //const url = this.api + 'WSGeneral.asmx/GenerateRazorOrderID';
    const url = this.api + 'WSGeneral.asmx/GenerateRazorOrderID';
    const headers = new HttpHeaders({ 'Content-Type': 'application/json' });
    return this.http.post<any>(url, body.toString(), { headers }).toPromise();
  }
}



